// stories/o-footer.stories.ts
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import type { OFooter } from '../components/o-footer/o-footer'; // Adjust the import path as needed

export default {
	title: 'Components/Footer'
} as Meta;

const Template: StoryObj<OFooter> = {
	parameters: {
		layout: 'centered',
		backgrounds: {
			default: 'light' // Set light as the default background
		},
		docs: {
			source: {
				transform: (_) => {
					return `
// Vue Code Snippet
  -----------------------
  
  import { OFooter} from "@oui/vue";
  
  <o-footer>
    <div>© 2024 OEConnection. All rights reserved. (6.0.1)</div>
  </o-footer>
  
// React Code Snippet
  -----------------------
  
  import { OFooter} from "@oui/react";

  <OFooter>
    <div>© 2024 OEConnection. All rights reserved. (6.0.1)</div>
  </OFooter>
            `;
				}
			}
		}
	},
	render: () => html`
		<o-footer>
			<div>© 2024 OEConnection. All rights reserved. (6.0.1)</div>
		</o-footer>
	`
};

export const Default = {
	...Template,
	parameters: {
		layout: 'fullscreen',
		docs: {
			description: {
				story: 'A default <strong>footer</strong>.'
			}
		}
	}
};
